.SearchResults {
  width: 46%;
  overflow-y: scroll;
  padding: .88rem;
  background-color: rgba(80, 80, 80, 0.7);
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}



